const COLORS = [
  '#16a085',
  '#27ae60',
  '#2c3e50',
  '#f39c12',
  '#e74c3c',
  '#9b59b6',
  '#FB6964',
  '#342224',
  '#472E32',
  '#BDBB99',
  '#77B1A9',
  '#73A857',
];

/**
 * Get random colors from array COLORS
 * @return {string} - a selected color from COLORS list
 */
const getRandomIndex = () => {
  const result = COLORS[Math.floor(Math.random() * COLORS.length)];
  return result;
};

export default getRandomIndex;
